.profile__button {
    border: none;
    background-color: #ffff;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
    border-radius: 4px;
    align-self: center;
}

.profile__button:hover {
    filter: none !important;
}

