.profile__image__wrapper {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    overflow: hidden;
    transition: transform 0.3s ease;
    position: relative;
}

.profile__image__wrapper img {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.profile__image__wrapper:hover {
    transform: scale(1.1);
}