.menu__category__name__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
    border-radius: 20px;
}

.menu__category__name__gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.66;
    background: linear-gradient(231.36deg, rgba(26, 0, 0, 0) 35.77%, #1A0000 90.7%);
    border-radius: 20px;
}

.menu__category__image__wrapper {
    display: flex;
    justify-content: start;
    align-items: end;

    cursor: pointer;
    user-select: none;
    position: relative;

    background-repeat: no-repeat;
    background-size: 100% auto;
    border-radius: 20px;
    width: 300px;
    height: 100px;
    
    transition: width 0.5s;
}

.menu__category__image__not__active:hover {
    width: 340px;
    transition: width 0.5s;
}

.menu__category__image__active {
    width: 380px;
    transition: width 0.3s;
}



.menu__category__name__text {
    color: #ffff;
    font-family: Open Sans;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    text-align: center;

    margin: 0 0 20px 20px;
    z-index: 1;
}